import React from 'react';
import { Row, Col, Card, Form, Button, InputGroup, FormControl, DropdownButton, Dropdown } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";

import Switch from "react-switch";

import ErrorBody from '../../App/components/Reusables/ErrorBody';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import Loader from '../../App/components/Reusables/Loader'
import { validateTaxCreation } from '../../validation/tax';
import { createTax, updateTax, getTax } from '../../api/misc';

const Update = (props) => {

    const [loading, setLoading] = React.useState(true)
    const [submitting, setSubmitting] = React.useState(false);
    const [taxData, setTaxData] = React.useState({
        id: '',
        name: '',
        type: '',
        rate: '',
        code: '',
        isActive: false,
    })


    const [errors, setErrors] = React.useState({});


    React.useEffect(() => {
        const controller = new AbortController();
        (async () => {
            await getTax({ action: 'get', tax_id: props.match.params.id }, controller.signal)
                .then(async (res) => {
                    console.log(res);
                    if (res.json.status) {
                        const tax = res.json.taxes[0];
                        await setTaxData({
                            ...taxData,
                            id: tax.id,
                            name: tax.name,
                            type: tax.tax_type,
                            rate: tax.rate,
                            code: tax.code,
                            isActive: tax.is_active,
                        });
                        await setLoading(false)
                    } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
                }).catch((err) => {
                    console.log(err)
                });
        })()
        return () => controller.abort();
    }, [])


    const amendErrorOf = (field) => {
        setErrors({ ...errors, [field]: '' })
    }



    const _taxDataChange = (e) => {
        amendErrorOf(e.target.name)
        setTaxData({
            ...taxData,
            [e.target.name]: e.target.value
        })
    }

    const _activeChange = (checked) => {
        console.log(checked);
        setTaxData({ ...taxData, isActive: checked })
    }
    const _submit = () => {

        const { isValid, errors } = validateTaxCreation(taxData);

        if (!isValid) {
            console.log(errors);
            setErrors({ ...errors })
            return;
        };
        setSubmitting(true)
        //if no errros api call
        const requestJson = {
            "action": "update",
            "tax_id": taxData.id,
            "tax_type": taxData.type,
            "name": taxData.name,
            "rate": parseFloat(taxData.rate),
            "code": taxData.code,
            "is_active": taxData.isActive
        }
        console.log(requestJson);
        updateTax(requestJson)
            .then((res) => {
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Tax updated successfully!', background: '#dffff0' })
                    props.history.push('/taxes');
                } else {
                    setSubmitting(false)
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                setSubmitting(false)
                console.log(err)
            });
    }


    if (loading) return <Loader />
    return (
        <Aux>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <h5>Create Tax</h5>
                            <hr />
                            <Row>
                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Tax Name*</Form.Label>
                                        <Form.Control name="name" value={taxData.name} onChange={_taxDataChange} type="text" placeholder="Tax Name" />
                                        <ErrorBody>{errors.name}</ErrorBody>
                                    </Form.Group>
                                </Col>
                                <Col md={6}>

                                    <Form.Group  >
                                        <Form.Label>Tax Type*</Form.Label>
                                        <Form.Control name="type" value={taxData.type} onChange={_taxDataChange} as="select">

                                            <option value='' disabled >Select...</option>
                                            <option value='normal'  >Normal</option>
                                            <option value='fixed'  >Fixed</option>
                                        </Form.Control>
                                        <ErrorBody>{errors.type}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={3}>
                                    <Form.Group  >
                                        <Form.Label>Rate*</Form.Label>
                                        <Form.Control name="rate" value={taxData.rate} onChange={_taxDataChange} type="number" placeholder="Rate" />
                                        <ErrorBody>{errors.rate}</ErrorBody>
                                    </Form.Group>
                                </Col>


                                <Col md={3}>
                                    <Form.Group  >
                                        <Form.Label>Code</Form.Label>
                                        <Form.Control name="code" value={taxData.code} onChange={_taxDataChange} type="text" placeholder="Code" />
                                        <ErrorBody>{errors.code}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group className="d-flex flex-column justify-content-end">
                                        <Form.Label style={{ marginBottom: '15px' }}>Is Active?</Form.Label>
                                        <Switch onChange={_activeChange} checked={taxData.isActive} />
                                    </Form.Group>
                                </Col>


                            </Row>
                            <div className="d-flex justify-content-end" >
                                <Button
                                    variant="warning"
                                    onClick={() => { props.history.push('/taxes') }}
                                >
                                    Cancel
                                    </Button>
                                <Button
                                    variant="primary"
                                    onClick={_submit}
                                >
                                    {submitting ? <><i className="fa fa-spinner text-c-white f-15 fa-pulse m-l-1"></i>Submitting</> : 'Submit'}

                                </Button>
                            </div>

                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Aux>
    );
}

export default Update;
